.checkoutProduct{
    display: flex;
    margin-top: 20px;
}
.checkoutProduct__info{
    padding-left: 20px;
    

}
.checkoutProduct__info>button{
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    bottom: 2px;
}
.checkoutProduct__image{
    object-fit: contain;
    height: 180px;
}
.checkoutProduct__rating{
    display: flex;
}
.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
    margin-top: 0px;
}
