.login{
    background-color: white;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login__logo{
    margin-top: 20px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-right: auto;
    margin-left: auto;
}
.login__container{
    width: 300px;
    height: fit-content;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
}
.login__container>h1{
    margin: 0px;
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container>form>h5{
    margin-bottom: 5px;
}
.login__container > form >input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width:98%

}
.login__container>p{
    margin-top: 15px;
    font-size: 12px;
}
.login__container>p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signInButton{
    background: #f0c14b;
    border: 1px solid;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    bottom: 2px;
    border-radius: 2px;
    width: 100%;
    margin-top: 10px;
    padding: 5px;
    font-weight: bold;
}
.login__registerButton{
    padding: 5px;
}